import React from 'react';
import { LoaderContainer, LoaderPage, LoaderSpinner } from './LoadingStyles';

const Loading = ({ errors }) => {
  return (
    <LoaderPage>
      <LoaderContainer>
        {errors ? (
           <div>Wystpi bd</div>
        ) : (
          <>
            <LoaderSpinner />
            <div>adowanie...</div>
          </>
        )}
      </LoaderContainer>
    </LoaderPage>
  );
};

export default Loading;
